import React, { Component, Fragment,useEffect,useContext } from 'react'
import Spinner from '../components/layout/Spinner';
import { Link } from 'react-router-dom';
import Repos from '../components/repos/Repos';
import GithubContext from '../context/github/GithubContext';

const User = ({match}) => {
    const githubContext = useContext(GithubContext);
    const {loading,user,getUser,repos,getUserRepo} = githubContext;
    useEffect(() => {
        getUser(match.params.login);
        getUserRepo(match.params.login);
    },[])
    const {
            name,
            avatar_url,
            html_url,
            blog,
            location,
            bio,
            company,
            login,
            followers,
            following,
            public_repos,
            public_gists,
            hireable
    } = user;
    if(loading) return <Spinner/>
    return (
            <Fragment>
            <Link to = "/" className = "btn btn-light">Back To Search</Link>
            Hireable: {' '}
            {hireable ? <i className = "fas fa-check text-success"/> 
            : <i className = "fas fa-times-circle text-danger"/>}
            <div className = "card grid-2">
            <div className = "all-center">
            <img
            src = {avatar_url}
            className = "round-img"
            style = {{width : "150px"}}
            />
            <h1>{name}</h1>
            <p>Location : {location}</p>
            </div>
            <div>
                                {bio && <Fragment>
                                    <h3>Bio</h3>
                                    <p>{bio}</p>    
                                </Fragment>}
                                <a href = {html_url} target = "_blank" className = "btn btn-dark my-1">Visit Github Profile</a>
                                <ul>
                                    <li>
                                        {login && <Fragment>
                                            <strong>Username :</strong> {login}    
                                        </Fragment>}
                                    </li>
                                    <li>
                                        {company && <Fragment>
                                            <strong>Company :</strong> {company}    
                                        </Fragment>}
                                    </li>
                                    <li>
                                        {blog && <Fragment>
                                            <strong>Website :</strong> {blog}    
                                        </Fragment>}
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className = "card text-center">
                            <div className = "badge badge-primary">
                                Followers: {followers}  
                            </div>
                            <div className = "badge badge-success">
                                Following: {following}  
                            </div>
                            <div className = "badge badge-light">
                                Public Repos: {public_repos}  
                            </div>
                            <div className = "badge badge-dark">
                                Public Gists: {public_gists}  
                            </div>
                        </div>
                        <Repos repos = {repos}/>
                    </Fragment>
                )
}

